@use "./mixin" as m;

section.comp-current {
  @media screen and (max-width: 900px) {
    margin-top: 20px !important;
  }
  .section-inner {
    .current-output {
      position: relative;
      border-radius: 10px;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-size: cover;
      background-position: center;
      color: #fff;
      @media screen and (min-width: 900px) {
        height: 100%;
      } //section-inner min 900
      @media screen and (max-width: 900px) {
        padding: 10px;
      } //section-inner min 900

      button.reload {
        position: absolute;
        width: 40px;
        height: 40px;
        right: 5%;
        top: 15%;
        font-size: 1.2em;
        opacity: 0.7;
        transition: all 0.5s;
        &:active,
        &:hover {
          opacity: 1;
          transform: rotate(180deg);
        }
      } //reload

      address {
        display: flex;
        overflow: hidden;
        margin-bottom: 10px;
        width: min(90%, 350px);
        span {
          white-space: nowrap;
          display: inline-block;
          animation: marqee 10s linear infinite;
        }
      } //address
     
      .circle-container {
        position: relative;
        width: min(85%, 300px);
        border-radius: 50%;
        background: rgba(#000, 0.2);
        display: flex;
        align-items: center;
        justify-content: center;
        @include m.square;
        div.sunset {
          position: absolute;
          width: 100%;
          height: 50%;
          left: 0;
          top: 0;
          svg {
            width: 100%;
            .sun {
              fill: rgb(243, 173, 44);
              offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
              animation: rotate-sun 10s infinite linear;
              /* offset-distance: 100%; */
              @keyframes rotate-sun {
                0% {
                  transform: rotate(0deg) scale(0.5);
                }
                100% {
                  transform: rotate(360deg) scale(0.7);
                }
              }
            } //sun
            .line {
              fill: none;
              stroke: rgba(255, 255, 255, 0.3);
              stroke-width: 3px;
            } //line
            .dash {
              fill: none;
              stroke: rgb(243, 173, 44);
              stroke-width: 6px;
              stroke-dasharray: 1;
              /* stroke-dashoffset: .5; */
            } //dash
          } //svg
          time {
            font-size: 0.85em;
            background: rgb(243, 173, 44);
            padding: 3px 5px;
            border-radius: 10px;
            display: inline-block;
            small {
              font-size: 0.8em;
              font-weight: 600;
              margin-right: 2px;
            } //small
            &.riseTime {
              transform: translateX(-23%);
            }
            &.setTime {
              float: right;
              transform: translateX(23%);
            }
          } //time
        } //sunset
      }
      .circle-container-inner {
        @include m.posLT(absolute, 50%, 50%, -50%, -50%);
        width: 90%;
        .temp {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          span{
            position: absolute;
            transform: translate(50px, 10px);
            font-weight: 600;
          }//span
          em {
            font-size: 3em;
            margin-right: -60px;
            position: relative;
            z-index: 1;
            text-shadow: 3px 3px 3px rgb(250, 133, 23);
           
            sup {
              font-size: 0.7em;
            }//sup
          }//em
          img {
            width: 100px;
            mask-image: url(../assets/img/black.png);
            mask-size: 100%;
            mask-position: center;
            mask-repeat: no-repeat;
          } //img 
          span {
            position: absolute;
            transform: translate(40px, 10px);
          }
        } //temp
      
        .desc {
          margin-top: -20px;
          text-align: center;
          font-size: 1.3em;
        }//desc
        .etc {
          width: 65%;
          margin: 5px auto;
          text-align: center;
          box-sizing: border-box;
          padding: 5px;
          border-radius: 10px;
          background: rgba(#000, 0.3);
          span {
            display: inline-block;
            margin: 3px 5px;
            font-size: 0.9em;
            i {
              margin: 3px;
              font-size: 0.8em;
              &.fa-location-arrow {
                font-size: 7px;
              }
            } //i
            b {
              font-weight: 600;
            } //b
            small {
              font-size: 0.8em;
            }
          }
        } //etc
        .time-date {
          margin: -5px;
          text-align: center;
          color: #fff;
          time {
            display: block;
            &.time {
              font-size: 1.2em;
              small{
                font-size: 0.8em;
              }
            }
            &.date{
              font-size: 0.9em;
            }
          }
        } //.time-date
      } //circle-container-inner
    } //circle-container
  } //current-output
} //comp-current

@keyframes marqee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
