@use "./mixin" as m;

html {
  --sky: #8ed1f1;
  --color: #4378a3;
}

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

body {
  height: 100vh;
  background: var(--sky);
  &::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    background-image: url(../assets/img/weather-icos.gif);
    background-size: 900px;
    opacity: 0.3;
    animation: bg 5000s linear infinite;
    @keyframes bg {
      0% {
        background-position: 0 0;
      }
      100% {
        background-position: -50000px -50000px;
      }
    } //keyframes
  } //body:hover
} //body

h1 {
  perspective: 700px;
  position: absolute;
  left: 50%;
  top: 0%;
  width: min(40%, 200px);
  transform: translate(-50%, -60%);
  img {
    width: 100%;
    opacity: 0.7;
    animation: rotate 10s infinite linear;
  } //img
  @keyframes rotate{
    0%{transform: rotateX(0deg)}
    30%{transform: rotateX(360deg)}
    100%{transform: rotateX(360deg)}
  }
} //h1

#root {
  @include m.posLT(fixed, 50%, 50%, -50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background: rgba(#fff, 0.7);
  border-radius: 10px;
  padding: 5px;
  box-sizing: border-box;
  border: 1px solid rgba(#fff, 0.5);
  animation: grow 2s infinite alternate linear;
  @media screen and (min-width: 900px) {
    min-height: 700px;
  }
  @keyframes grow {
    0% {box-shadow: 0 0 10px 0 rgba(#fff, 0.3);}
    100% {box-shadow: 0 0 15px 5px rgba(#fff, 0.7);}
  }
  .app-inner {
    height: 100%;
    position: relative;
    @media screen and (min-width: 800px) {
      display: flex;
      flex-wrap: wrap;
      overflow: hidden;
    }
    @media screen and (max-width: 800px) {
      overflow: hidden;
      overflow-y: auto;
      overflow-x: hidden;
    }
    section {
      box-sizing: border-box;
      @media screen and (min-width: 900px) {
        width: 50%;
        height: 50%;
      } //section min900
      @media screen and (max-width: 900px) {
        margin: 20px 0;
      } //section max900
      .section-inner {
        position: relative;
        padding: 5px;
        box-sizing: border-box;
        border-radius: 10px;
        min-height: 250px;
        @media screen and (min-width: 900px) {
          overflow: auto;
          height: 100%;
        }
        .preloader{
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          background: var(--sky);
        }//loader
        h2{
          text-transform: uppercase;
          font-weight: 600;
          padding: 5px 10px;
          color: #FFF;
          background: var(--color);
          border-radius: 5px;
          top: 0px;
          @media screen and (min-width:900px){
            position: sticky;
            top: 0px;
            left: 0px;
            z-index: 1;
          }
        }//h2
      } //.section-inner
    } //section
  } //.app-inner
} //#root



.app-inner , body, .section-inner{
  @media (hover: hover) {
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
      background-color: #fff;
    }
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--color);
    }
  } //media hover
} //.app-inner

/* 로딩로고 css------------------------ */

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
}
.loader {
  color: #ffffff;
  font-size: 11px;
  text-indent: -99999em;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em;
  transform: scale(0.5) translateZ(0);
}
.loader:before,
.loader:after {
  position: absolute;
  content: '';
}
.loader:before {
  width: 5.2em;
  height: 10.2em;
  background: var(--sky);
  border-radius: 10.2em 0 0 10.2em;
  top: -0.1em;
  left: -0.1em;
  -webkit-transform-origin: 5.1em 5.1em;
  transform-origin: 5.1em 5.1em;
  -webkit-animation: load2 2s infinite ease 1.5s;
  animation: load2 2s infinite ease 1.5s;
}
.loader:after {
  width: 5.2em;
  height: 10.2em;
  background: var(--sky);
  border-radius: 0 10.2em 10.2em 0;
  top: -0.1em;
  left: 4.9em;
  -webkit-transform-origin: 0.1em 5.1em;
  transform-origin: 0.1em 5.1em;
  -webkit-animation: load2 2s infinite ease;
  animation: load2 2s infinite ease;
}
@-webkit-keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

/* 로딩로고 css 끝------------------------ */